import { useState, useEffect } from 'react';
import { Form, FormField } from '../components/ppm/cms/form-builder/FormBuilderTypes';

const STORAGE_KEY = 'sbu_cms_builder_forms';

const DEFAULT_FORMS: Form[] = [
    {
        id: 'default_ghi_danh_nhap_hoc',
        title: 'Ghi Danh Nhập Học 2',
        fields: [
            { id: 'field_1', name: 'Họ và Tên', type: 'short_answer', required: true, placeholder: 'Nhập họ và tên' },
            { id: 'field_2', name: 'Pháp Danh', type: 'short_answer', placeholder: 'Nhập pháp danh' },
            { id: 'field_3', name: 'Ngày Sinh', type: 'short_answer', placeholder: 'dd/mm/yyyy', helpText: 'Định dạng: dd/mm/yyyy' },
            { id: 'field_4', name: 'Giới Tính', type: 'multiple_choice', options: ['Nam', 'Nữ'], required: true },
            { id: 'field_5', name: 'Email', type: 'short_answer', required: true, placeholder: 'example@email.com', validation: { mode: 'email' } },
            { id: 'field_6', name: 'Số Điện Thoại', type: 'short_answer', required: true, placeholder: '0xxx xxx xxx' },
            { id: 'field_7', name: 'Chương Trình Học', type: 'dropdown', options: ['Sơ Cấp', 'Trung Cấp', 'Cao Đẳng'], required: true },
            { id: 'field_8', name: 'Hình Thức Tham Gia', type: 'dropdown', options: ['Trực Tiếp', 'Trực Tuyến'], required: true }
        ]
    },
    {
        id: 'default_ghi_danh_thien',
        title: 'Ghi Danh Thiền 3',
        fields: [
            { id: 'field_t1', name: 'Họ và Tên', type: 'short_answer', required: true, placeholder: 'Nhập họ và tên' },
            { id: 'field_t2', name: 'Pháp Danh', type: 'short_answer', placeholder: 'Nhập pháp danh' },
            { id: 'field_t3', name: 'Ngày Sinh', type: 'short_answer', placeholder: 'dd/mm/yyyy', helpText: 'Định dạng: dd/mm/yyyy' },
            { id: 'field_t4', name: 'Giới Tính', type: 'multiple_choice', options: ['Nam', 'Nữ'], required: true },
            { id: 'field_t5', name: 'Email', type: 'short_answer', required: true, placeholder: 'example@email.com', validation: { mode: 'email' } },
            { id: 'field_t6', name: 'Số Điện Thoại', type: 'short_answer', required: true, placeholder: '0xxx xxx xxx' },
            { id: 'field_t7', name: 'Khóa Thiền', type: 'dropdown', options: ['Khóa Thiền Căn Bản', 'Khóa Thiền Nâng Cao'], required: true },
            { id: 'field_t8', name: 'Hình Thức Tham Gia', type: 'dropdown', options: ['Tuần Tự', 'Trọn Khóa'], required: true }
        ]
    }
];

export const useFormData = () => {
    const [forms, setForms] = useState<Form[]>([]);
    const [loading, setLoading] = useState(true);

    // Load forms from localStorage on mount
    useEffect(() => {
        const savedForms = localStorage.getItem(STORAGE_KEY);
        if (savedForms) {
            try {
                const parsed = JSON.parse(savedForms);
                if (Array.isArray(parsed) && parsed.length > 0) {
                    setForms(parsed);
                } else {
                    setForms(DEFAULT_FORMS);
                    localStorage.setItem(STORAGE_KEY, JSON.stringify(DEFAULT_FORMS));
                }
            } catch (e) {
                console.error("Failed to parse saved forms", e);
                setForms(DEFAULT_FORMS);
            }
        } else {
            // Seed with defaults if nothing saved
            setForms(DEFAULT_FORMS);
            localStorage.setItem(STORAGE_KEY, JSON.stringify(DEFAULT_FORMS));
        }
        setLoading(false);
    }, []);

    // Save forms to localStorage whenever they change
    const saveToStorage = (updatedForms: Form[]) => {
        localStorage.setItem(STORAGE_KEY, JSON.stringify(updatedForms));
        setForms(updatedForms);
    };

    const createForm = (title: string, fields: FormField[] = []): Form => {
        const newForm: Form = {
            id: `form_${Date.now()}`,
            title: title || 'Untitled Form',
            fields: fields
        };
        const updated = [...forms, newForm];
        saveToStorage(updated);
        return newForm;
    };

    const updateForm = (formId: string, updates: Partial<Form>) => {
        const updated = forms.map(f => f.id === formId ? { ...f, ...updates } : f);
        saveToStorage(updated);
    };

    const deleteForm = (formId: string) => {
        const updated = forms.filter(f => f.id !== formId);
        saveToStorage(updated);
    };

    const renameForm = (formId: string, newTitle: string) => {
        updateForm(formId, { title: newTitle });
    };

    const addField = (formId: string, field: FormField) => {
        const currentForm = forms.find(f => f.id === formId);
        if (currentForm) {
            const updatedFields = [...currentForm.fields, field];
            updateForm(formId, { fields: updatedFields });
        }
    };

    const removeField = (formId: string, fieldId: string) => {
        const currentForm = forms.find(f => f.id === formId);
        if (currentForm) {
            const updatedFields = currentForm.fields.filter(f => f.id !== fieldId);
            updateForm(formId, { fields: updatedFields });
        }
    };

    return {
        forms,
        loading,
        createForm,
        updateForm,
        deleteForm,
        renameForm,
        addField,
        removeField
    };
};
