import React from 'react';
import { Form, FormField } from '../ppm/cms/form-builder/FormBuilderTypes';

type FormRendererProps = {
    form: Form;
    disabled?: boolean;
};

const FormRenderer: React.FC<FormRendererProps> = ({ form, disabled = false }) => {
    const renderField = (field: FormField) => {
        const commonProps = {
            id: field.id,
            name: field.id, // Use ID as name for grouping
            required: field.required,
            disabled: disabled
        };

        switch (field.type) {
            case 'short_answer':
                return (
                    <div key={field.id} className="form-group">
                        <label htmlFor={field.id}>
                            {field.name} {field.required && <span className="required" style={{ color: '#e74c3c' }}>*</span>}
                        </label>
                        <input type="text" {...commonProps} placeholder={field.placeholder || ''} />
                        {field.helpText && <span className="help-text-hint" style={{ fontSize: '0.75rem', color: '#666', marginTop: '4px' }}>{field.helpText}</span>}
                    </div>
                );
            case 'paragraph':
                return (
                    <div key={field.id} className="form-group" style={{ gridColumn: 'span 2' }}>
                        <label htmlFor={field.id}>
                            {field.name} {field.required && <span className="required" style={{ color: '#e74c3c' }}>*</span>}
                        </label>
                        <textarea {...commonProps} rows={4} placeholder={field.placeholder || ''} />
                        {field.helpText && <span className="help-text-hint" style={{ fontSize: '0.75rem', color: '#666', marginTop: '4px' }}>{field.helpText}</span>}
                    </div>
                );
            case 'multiple_choice':
                return (
                    <div key={field.id} className="form-group">
                        <label>{field.name} {field.required && <span className="required" style={{ color: '#e74c3c' }}>*</span>}</label>
                        <div className="radio-group">
                            {field.options.map((option, idx) => (
                                <label key={idx}>
                                    <input type="radio" name={field.id} value={option} disabled={disabled} />
                                    {option}
                                </label>
                            ))}
                        </div>
                        {field.helpText && <span className="help-text-hint" style={{ fontSize: '0.75rem', color: '#666', marginTop: '4px' }}>{field.helpText}</span>}
                    </div>
                );
            case 'dropdown':
                return (
                    <div key={field.id} className="form-group">
                        <label htmlFor={field.id}>
                            {field.name} {field.required && <span className="required" style={{ color: '#e74c3c' }}>*</span>}
                        </label>
                        <select {...commonProps}>
                            <option value="">{field.placeholder || '-- Chọn --'}</option>
                            {field.options.map((option, idx) => (
                                <option key={idx} value={option}>{option}</option>
                            ))}
                        </select>
                        {field.helpText && <span className="help-text-hint" style={{ fontSize: '0.75rem', color: '#666', marginTop: '4px' }}>{field.helpText}</span>}
                    </div>
                );
            case 'checkboxes':
                return (
                    <div key={field.id} className="form-group" style={{ gridColumn: 'span 2' }}>
                        <label>{field.name} {field.required && <span className="required" style={{ color: '#e74c3c' }}>*</span>}</label>
                        <div className="radio-group" style={{ flexWrap: 'wrap', height: 'auto', padding: '10px 0' }}>
                            {field.options.map((option, idx) => (
                                <label key={idx} style={{ marginBottom: '5px' }}>
                                    <input type="checkbox" name={field.id} value={option} disabled={disabled} style={{ width: '18px', height: '18px' }} />
                                    {option}
                                </label>
                            ))}
                        </div>
                        {field.helpText && <span className="help-text-hint" style={{ fontSize: '0.75rem', color: '#666', marginTop: '4px' }}>{field.helpText}</span>}
                    </div>
                );
            case 'date':
                return (
                    <div key={field.id} className="form-group">
                        <label htmlFor={field.id}>
                            {field.name} {field.required && <span className="required" style={{ color: '#e74c3c' }}>*</span>}
                        </label>
                        <input type="date" {...commonProps} />
                        {field.helpText && <span className="help-text-hint" style={{ fontSize: '0.75rem', color: '#666', marginTop: '4px' }}>{field.helpText}</span>}
                    </div>
                );
            case 'time':
                return (
                    <div key={field.id} className="form-group">
                        <label htmlFor={field.id}>
                            {field.name} {field.required && <span className="required" style={{ color: '#e74c3c' }}>*</span>}
                        </label>
                        <input type="time" {...commonProps} />
                        {field.helpText && <span className="help-text-hint" style={{ fontSize: '0.75rem', color: '#666', marginTop: '4px' }}>{field.helpText}</span>}
                    </div>
                );
            case 'file_upload':
                return (
                    <div key={field.id} className="form-group">
                        <label htmlFor={field.id}>
                            {field.name} {field.required && <span className="required" style={{ color: '#e74c3c' }}>*</span>}
                        </label>
                        <input type="file" {...commonProps} />
                        {field.helpText && <span className="help-text-hint" style={{ fontSize: '0.75rem', color: '#666', marginTop: '4px' }}>{field.helpText}</span>}
                    </div>
                );
            default:
                return null;
        }
    };

    return (
        <div className={`form-grid-renderer ${disabled ? 'disabled' : ''}`}>
            {form.fields.map(field => renderField(field))}
        </div>
    );
};

export default FormRenderer;
